import { useMemo } from 'react';
import { useQuery } from '@tanstack/react-query';
import { t } from 'i18next';
import { useSelector } from 'react-redux';
import { getGlobalSettingsParams } from '../../store/globalSettingsSlicer/selectors';
import { fetchLeadTime } from '../../utils/ApiCalls/ApiCalls';
import { useWindowSize } from '../../utils/threekitHooks';

export const useAvaibility = (attributesObject?: any, attributeNames?: any) => {
  const { isMobile } = useWindowSize();
  const params = useSelector(getGlobalSettingsParams);
  const { storeCode = '', country = '' } = params || {};

  const values = useMemo(
    () =>
      attributesObject?.[attributeNames?.[0]]?.data?.values?.filter(
        (v: any) => !v?.name?.startsWith('Choose')
      ) || [],
    [attributesObject, attributeNames]
  );


  const sku = useMemo(
    () => values?.map((item: Record<string, any>) => item.sku) || [],
    [values]
  );

  const queryKey = useMemo(
    () => ['productLeadTime', storeCode, country,sku],
    [storeCode, country,sku]
  );

  const {
    data: leadTime,
    isLoading: isLoadingLeadTime,
    isError,
    error,
  } = useQuery({
    queryKey,
    queryFn: () => fetchLeadTime({ storeCode, sku, country, isMobile }),
    staleTime: 300000,
    enabled: !!sku && !!storeCode && !!country && !!t,
    refetchOnWindowFocus: false,
    retry: false,
  });
  console.log(leadTime)

  return useMemo(() => {
    const isLoading = isLoadingLeadTime;
    let orderedValues = [];

    if (!isLoading) {
      console.log(leadTime);
      const skuAv = (a: string) => {
        const test = leadTime?.items.find(
          (element: Record<string, string>) => element.sku === a
        );
        if (test) {
          return test?.avaibility;
        }
        return 'N';
      };
      orderedValues = values
        .sort((a: any, b: any) =>
          skuAv(a?.sku) === skuAv(b?.sku) ? 0 : skuAv(a?.sku) ? -1 : 1
        )
        .map((item: any) => ({ ...item, avaibility: skuAv(item.sku) }));
    }

    return {
      orderedValues,
      leadTime,
      isLoading,
      isError,
      error,
    };
  }, [leadTime, isLoadingLeadTime, isError, error]);
};

export default useAvaibility;
